@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500;600;700&display=swap);
body{margin:0;font-family:"Montserrat", sans-serif;font-weight:500;color:1368px}*{box-sizing:border-box;margin:0;padding:0}ul{list-style-type:none}a{color:#FF6060}.wrapper{width:100%;padding:20px}

.header{display:flex;justify-content:space-between;align-items:center;width:1240px;margin:25px auto;height:68px}@media screen and (max-width: 1368px){.header{width:100%}}

@media screen and (max-width: 1368px){.logo{width:50%}}@media screen and (max-width: 1368px){.logo img{width:80%}}

.navigation{width:310px;height:35px}@media screen and (max-width: 1368px){.navigation{width:50%;height:auto}}.navigation__list{display:flex;justify-content:space-around;align-items:center}.navigation__link{font-size:1.4em;text-decoration:none}@media screen and (max-width: 1368px){.navigation__link{font-size:1em}}.navigation__link:hover{text-decoration:underline}

.footer{display:flex;flex-direction:column;align-items:center;justify-content:flex-end;width:100%;height:209px;background-color:#000}.footer__title{margin-top:30px;margin-bottom:40px;color:#fff;font-weight:500}@media screen and (max-width: 1368px){.footer img{width:100%}}

.main{width:1240px;margin:0 auto}@media screen and (max-width: 1368px){.main{width:100%}}.main--404{display:flex;flex-direction:column;align-items:center;justify-content:center;height:900px}.main--404__title{font-size:9em}.main--404__subtitle{font-size:1.6em;font-weight:500}

.banner{position:relative;display:flex;justify-content:center;align-items:center;width:1240px;height:223px;margin:25px 0;border-radius:25px;overflow:hidden}@media screen and (max-width: 1368px){.banner{width:100%;height:110px;padding:25px;border-radius:10px}}.banner__overlay{position:absolute;width:1240px;height:223px;background:#000000;mix-blend-mode:darken;opacity:0.3}.banner__title{position:absolute;font-size:2.6em;font-weight:500;color:#fff}@media screen and (max-width: 1368px){.banner__title{font-size:1.5em}}

.toggle{width:95%;margin:25px 0;border-radius:0 0 10px 10px;background-color:#F6F6F6}.toggle--about-page{width:80%;margin:25px auto}.toggle__title{display:flex;justify-content:space-between;align-items:center;height:52px;border-radius:10px;background-color:#FF6060;cursor:pointer}.toggle__title h3{margin:0 20px;font-weight:400;color:#fff}.toggle__title::after{margin-right:15px;content:"\f078";font-family:"Font Awesome 5 Free";font-size:1.4em;font-weight:700;color:#fff}.toggle__title--onClick::after{content:"\f077"}.toggle__content{padding:20px;font-weight:400}

.gallery{display:flex;flex-wrap:wrap;justify-content:center;grid-gap:50px;gap:50px;width:100%;padding:50px;border-radius:25px;background-color:#f6f6f6}@media screen and (max-width: 1368px){.gallery{padding:0;margin-bottom:20px;background-color:transparent}}

.card{position:relative;width:340px;height:340px;border-radius:10px;background:linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(0,0,0,0.5) 100%);overflow:hidden;cursor:pointer}@media screen and (max-width: 1368px){.card{height:255px}}.card__title{position:absolute;left:10px;bottom:10px;font-size:1.2em;color:#fff}.card__picture{width:100%;height:100%;object-fit:cover}

.carousel{position:relative;height:415px;margin:25px 0;background-color:#FF6060;border-radius:25px;overflow:hidden}.carousel__picture{display:none;width:100%;object-fit:cover}.carousel__picture[visible="true"]{display:block}.carousel__nav{position:absolute;top:0;display:flex;justify-content:space-between;align-items:center;width:100%;height:100%}.carousel__nav-previous::before,.carousel .carousel__nav-next::after{width:40px;height:40px;font-family:"Font Awesome 5 Free";font-size:4em;font-weight:700;color:#fff;cursor:pointer}.carousel__nav-previous::before{content:"\f053";margin-left:10px}.carousel__nav-next::after{content:"\f054";margin-right:10px}

.information{width:auto;margin:15px 0}.information__title,.information__subtitle{font-weight:500}.information__subtitle{font-size:1em}

.tags__container{display:flex}.tags__item{display:flex;justify-content:center;align-items:center;width:115px;height:25px;margin-right:15px;border-radius:10px;background-color:#FF6060;font-size:0.7em;color:#fff}

.host{display:flex;align-items:center}.host__name{font-size:1.1em}.host__picture{width:64px;height:64px;margin-left:10px;border-radius:50px;background-color:#c4c4c4;overflow:hidden}.host__picture img{width:100%}

.rating{display:flex;justify-content:space-around;width:190px}


.container{display:flex;width:100%}.container__left,.container__right{display:flex;flex-direction:column;justify-content:space-between;width:100%}.container__left{align-items:flex-start}.container__right{align-items:flex-end}

